body {
  background: rgb(10, 10, 10);
  
}

.container {
  display: flex !important;
  flex-direction: column;   /* column */
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 400px;
  width: 200%;
  margin: auto;
  background-color: rgb(15, 29, 29);
}
.container > form {
  width: 50%;
  border: 1px solid #dfdfdf;
  padding: 40px;
  border-radius: 5px;
  background: #fff;
}
pre {
  width: 70%;
  color: #fff;
  font-size: larger;
}

button {
  background: #5a3fd3 ;
  color: white;
  border-radius: 3px;
}
button:hover {
  background-color: #3f1fd2;
}
p {
  color: red;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

